.statement {
  padding-top: 100px;
  background-color: #FBF9F8;

  &__title {
    margin-bottom: 40px; }

  &__list {
    display: flex;
    justify-content: space-between;
    margin-bottom: 144px; }

  &__item {
    width: calc(50% - 17px);
    background: rgba(255, 255, 255, 0.8);
    border: 1px solid #F7F4F2;
    box-shadow: 10px 10px 20px #F7F1EC;
    border-radius: 5px;
    padding-top: 28px;
    padding-bottom: 33px;
    display: flex;
    flex-direction: column; } }

.card {

  &__head {
    padding-left: 33px;
    border-bottom: 1px solid #E6E6EA;
    display: flex;
    align-items: center;
    margin-bottom: 40px; }

  &__img-wrap {
    overflow: hidden;
    margin-right: 22px; }

  &__img {
    display: block; }

  &__title {
    font-family: PTSerif,Arial,sans-serif;
    font-weight: 400; }

  &__text {
    margin-left: 55px;
    margin-right: 55px;
    margin-bottom: 40px; }

  &__link {
    margin-left: 55px;
    margin-right: 55px;
    display: block;
    text-align: center;
    font-size: rem(14);
    line-height: rem(16);
    font-weight: 500;
    color: $text;
    margin-top: auto;
    padding-top: 20px;
    padding-bottom: 23px;
    background-color: #F7F4F2;
    border-radius: 4px;
    transition: 0.2s;

    &:hover {
      background-color: #D0AE96;
      color: $white; } } }

.experience {
  display: flex;
  justify-content: space-around;

  &__img-wrap {
    width: 265px;
    overflow: hidden; }

  &__img {
    display: block; }

  &__list {
    display: flex;
    flex-direction: column;
    align-items: flex-start; }

  &__item {
    padding-top: 18px;
    padding-bottom: 18px;
    padding-left: 20px;
    padding-right: 24px;
    background: rgba(255, 255, 255, 0.6);
    border: 1px solid #F7F4F2;
    box-shadow: 10px 10px 20px #F7F1EC;
    border-radius: 5px;

    & + li {
      margin-top: 10px; } }

  &__text {

    & b {
      font-weight: 600; } }

  &__title {
    font-size: rem(34);
    line-height: rem(44);
    font-family: PTSerif,Arial,sans-serif;
    font-weight: 400;
    margin-bottom: 24px; } }
