.promo {
  background-color: #EFE5DC;
  padding-top: 176px;
  padding-bottom: 103px;
  position: relative;
  overflow: hidden;

  &__inner {
    z-index: 1;
    position: relative; }

  &__left {
    display: flex;
    flex-direction: column;
    align-items: flex-start; }

  &__title {
    margin-bottom: 30px; }

  &__text {
    margin-bottom: 40px; }

  &__link {
    display: flex;
    align-items: center;
    padding-top: 16px;
    padding-bottom: 16px;
    padding-left: 90px;
    padding-right: 20px;
    position: relative;
    border-radius: 10px;
    border: 1px solid #F3E0CF;
    background-color: rgba($white, 0.5);

    & + a {
      margin-top: 15px; } }

  &__link-img {
    display: block;
    position: absolute;
    bottom: 0;
    left: 13px; }

  &__link-text {
    font-weight: 500;
    color: $text; }

  &__img-wrap {
    position: absolute;
    right: 0;
    top: 0;
    width: 70%;
    max-width: 1000px; }

  &__img {
    width: 100%; } }

.title {
  font-family: PTSerif,Arial,sans-serif;
  font-weight: 400;

  &--bold {
    font-weight: 700; } }
