.help {
  padding-top: 100px;
  padding-bottom: 100px;

  &__title {
    margin-bottom: 50px; }

  &__list {
    display: flex;
    justify-content: space-between; }

  &__item {
    padding-top: 60px;
    padding-left: 40px;
    padding-right: 30px;
    padding-bottom: 60px;
    width: calc((100% / 3) - 10px);
    background-color: #FBF9F8;
    border: 1px solid rgba(124, 133, 157, 0.1);
    border-radius: 5px; }

  &__num {
    font-family: PTSerif,Arial,sans-serif;
    width: 115px;
    height: 115px;
    position: relative;
    border-radius: 50%;
    background-color: #F2EAE4;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 30px;

    & span {
      z-index: 1; }

    &::after {
      position: absolute;
      content: "";
      width: 85px;
      height: 85px;
      border: 1px solid #FFFFFF;
      box-shadow: 0px 4px 4px rgba(118, 107, 100, 0.15);
      background-color: #fff;
      border-radius: 50%; } }

  &__item-title {
    font-weight: 600;
    margin-bottom: 30px; } }
